import axios from 'axios'
import qs from "qs";
import { installInterceptors } from "./axios";
const $helper = axios.create({
    withCredentials: true,
    baseURL: process.env.NODE_ENV === "production" ? __next : "/",
});
installInterceptors($helper);

// 
let WikiPost = {
    // 
    newest(type, id, supply = 1) {
        if (!id) return;
        return $helper({
            method: "GET",
            url: `/api/wiki/post`,
            headers: { Accept: "application/prs.helper.v2+json" },
            params: { type: type, source_id: id, supply: supply },
        });
    },

    // 
    newests(type) {
        return $helper({
            method: "GET",
            url: `/api/wiki/posts/newest`,
            headers: { Accept: "application/prs.helper.v2+json" },
            params: { type: type },
        });
    },

    // 
    versions(type, id) {
        if (!id) return;
        return $helper({
            method: "GET",
            url: `/api/wiki/post/versions`,
            headers: { Accept: "application/prs.helper.v2+json" },
            params: { type: type, source_id: id },
        });
    },

    // 
    view(post_id) {
        if (!post_id) return null;
        return $helper({
            method: "GET",
            url: `/api/wiki/post/${post_id}`,
            headers: { Accept: "application/prs.helper.v2+json" },
        });
    },

    // /
    save(params) {
        return $helper({
            method: "POST",
            url: `/api/wiki/post`,
            headers: { Accept: "application/prs.helper.v2+json" },
            data: qs.stringify({ post: params }),
        });
    },

    // 
    myList(params) {
        return $helper({
            method: "GET",
            url: `/api/my/wiki/posts`,
            headers: { Accept: "application/prs.helper.v2+json" },
            params: params,
        });
    },

    // 
    myRemove(post_id) {
        if (!post_id) return null;
        return $helper({
            method: "PUT",
            url: `/api/my/wiki/post/${post_id}/remove`,
            headers: { Accept: "application/prs.helper.v2+json" },
        });
    },
};

// 
let WikiComment = {
    // 
    list(type, id) {
        if (!id) return;
        return $helper({
            method: "GET",
            url: `/api/wiki/comments`,
            headers: { Accept: "application/prs.helper.v2+json" },
            params: { type: type, source_id: id },
        });
    },

    // 
    save(params) {
        return $helper({
            method: "POST",
            url: `/api/wiki/comment`,
            headers: { Accept: "application/prs.helper.v2+json" },
            data: qs.stringify({ comment: params }),
        });
    },

    // 
    myList(params) {
        return $helper({
            method: "GET",
            url: `/api/my/wiki/comments`,
            headers: { Accept: "application/prs.helper.v2+json" },
            params: params,
        });
    },

    // 
    myRemove(comment_id) {
        if (!comment_id) return null;
        return $helper({
            method: "PUT",
            url: `/api/my/wiki/comment/${comment_id}/remove`,
            headers: { Accept: "application/prs.helper.v2+json" },
        });
    },
};

// 
function getBread(channel) {
    return $helper.get("api/breadcrumb/" + channel, {
        headers: { Accept: "application/prs.helper.v2+json" },
    });
}

export { WikiPost, WikiComment, getBread };
